  height: 100%;
  transform: translate3d(-100%, 0, 0); }

/* overlays for pusher and for level that gets covered */
.mp-pusher::after,
.mp-level::after,
.mp-level::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  opacity: 0; }

.mp-pusher::after,
.mp-level::after {
  background: rgba(0, 0, 0, 0.3);
  transition: opacity 0.3s, width 0.1s 0.3s, height 0.1s 0.3s; }

